.invslot {
  position: relative;
  display: inline-block;
  background: #8B8B8B no-repeat center center / 32px 32px;
  border: 2px solid;
  border-color: #373737 #FFF #FFF #373737;
  width: 36px;
  height: 36px;
  font-size: 16px;
  line-height: 1;
  text-align: left;
  vertical-align: bottom
}

.invslot::before,
.invslot::after {
  content: "";
  position: absolute;
  background-color: #8B8B8B;
  height: 2px;
  width: 2px;
  pointer-events: none
}

.invslot::before {
  bottom: -2px;
  left: -2px
}

.invslot::after {
  top: -2px;
  right: -2px
}

.invslot-large {
  padding: 8px;
  width:48px;
  height: 48px;
}

.invslot-item,
.invslot-item>a:first-child {
  position: relative;
  display: block;
  margin: -2px;
  padding: 2px;
  width: 32px;
  height: 32px
}

.invslot-item-image>a.new:first-child {
  background: url(https://static.wikia.nocookie.net/minecraft_gamepedia/images/4/44/InvSprite.png/revision/latest) no-repeat content-box;
  width: 32px;
  height: 32px;
  text-indent: -9999px;
  overflow: hidden
}

.invslot-stacksize {
  position: absolute;
  right: 0;
  bottom: 0;
  font-family: Minecraft, sans-serif !important;
  font-weight: normal !important;
  color: #FFF !important;
  text-shadow: 2px 2px 0 #3F3F3F;
  filter: dropshadow(color=#3F3F3F, offx=2, offy=2);
  z-index: 2
}

.invslot-plain {
  background-color: transparent;
  border: 0
}

.invslot-plain::before,
.invslot-plain::after {
  content: none
}

.invslot-plain>.invslot-item,
.invslot-plain>.invslot-item>a:first-child {
  margin: 0;
  padding: 0
}

.mc-hearts::before {
  content: ' ('
}

.mc-hearts::after {
  content: ')'
}

.mcui {
  display: inline-block;
  position: relative;
  background-color: #C6C6C6;
  border: 2px solid;
  border-color: #DBDBDB #5B5B5B #5B5B5B #DBDBDB;
  padding: 6px;
  text-align: left;
  white-space: nowrap;
  vertical-align: bottom
}

