.cube {
  --scale-factor: calc(var(--scale) * calc(32 / 300));
  width: calc( var(--scale-factor) * 300px);
  height: calc( var(--scale-factor) * 300px);
  overflow: hidden;
  
}
.face {
  transform-origin: 0 0;
  --scale-factor: calc(var(--scale) * calc(45 / 300));
  position: absolute;
  width: calc( var(--scale-factor) * 100px);
  height: calc( var(--scale-factor) * 100px);
  background-size: contain !important;
  --offset: translate(calc( var(--scale-factor) * -10px), calc( var(--scale-factor) * -30px));
  }
  .front {
    background: blue;
    transform: var(--offset) rotate(-30deg) skewX(-30deg) translate(calc( var(--scale-factor) * 130px), calc( var(--scale-factor) * 172px)) scaleY(0.864);
  }
  .top {
    background: red;
    transform: var(--offset) rotate(210deg) skew(-30deg) translate(calc( var(--scale-factor) * -200px), calc( var(--scale-factor) * -60px)) scaleY(0.864);
  }
  .side {
    background: green;
    transform: var(--offset) rotate(90deg) skewX(-30deg) scaleY(0.864) translate(calc( var(--scale-factor) * 68px), calc( var(--scale-factor) * -131px));
  }